import { BEFORE_MOUNT_KEY } from "./util/const.js";
// deno-lint-ignore no-explicit-any
const addHiddenItem = (target, key, hook) => {
    target[key] = (target[key] || []).concat(hook);
};
// deno-lint-ignore no-explicit-any
export const addMountHook = (target, hook) => {
    addHiddenItem(target, BEFORE_MOUNT_KEY, hook);
};
export default addHiddenItem;
