import { addMountHook } from "../add_hidden_item.js";
/**
 * is decorator adds the class names to the given element when it's mounted.
 * @param args The list of class names
 */
export default (...args) => 
// deno-lint-ignore ban-types
(Cls) => {
    addMountHook(Cls, (el) => {
        el.classList.add(...args);
    });
};
