import { triggerToElements } from "../util/event_trigger.js";
import check from "../util/check.js";
/**
 * Publishes the given event to the elements which has `sub:${event}` class.
 * For example `@pub('foo')` publishes the `foo` event to the elements
 * which have `sub:foo` class.
 * @param event The event name
 * @param targetSelector? The target selector. Default .sub\:{event}
 */
export default (event, targetSelector) => (
// deno-lint-ignore no-explicit-any
target, key, 
// deno-lint-ignore no-explicit-any
descriptor) => {
    const method = descriptor.value;
    const constructor = target.constructor;
    check(!!event, `Unable to publish empty event: constructor=${constructor.name} key=${key}`);
    const selector = targetSelector || `.sub\\:${event}`;
    descriptor.value = function () {
        const result = method.apply(this, arguments);
        triggerToElements(
        // deno-lint-ignore no-explicit-any
        [].concat.apply([], document.querySelectorAll(selector)), event, false, result);
        return result;
    };
};
